.display-onsiteupload{
  width: 204px;
  z-index: 9998;
  position: absolute;
  right: 0px;
  bottom: 0px;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  background-color: rgb(0 0 0 / 75%) !important;
  height: auto !important;
  margin: 10px;
}
.onsiteupload_top_left_position{
  top:0px;
  left: 0px;
}
.onsiteupload_top_right_position{
  top:0px;
  right: 0px;
}
.onsiteupload_bottom_left_position{
 bottom: 0px;
 left: 0px;
}
.onsiteupload_bottom_right_position{
  bottom: 0px;
  right: 0px;
 }

.onsiteupload_top_position{
  top:0px;
  }
  .onsiteupload_bottom_position{
   bottom: 0px;
  }

.onsite_barcode_img_{
  height: 100%;
  width: auto;
  max-width: 100%;
}

.onsite_barcode_{
  display: flex;
  flex-shrink: 1;
  align-items: center;
  height: 100%;
  min-width: 70px;
  object-fit: cover;
  border: 1px solid #fff;
  border-radius: 8px;
  background: #fff;
  padding: 5px 3px 5px 5px;
  justify-content: center;
}

.onsite_text_{
  display: flex;
  align-items: center;
  justify-content: start;
  text-align: center;
  margin-top: 10px;
  font-weight: 600;
}
@media screen and (max-width: 767px){
  .display-onsiteupload{
    height: 90px !important;
    flex-direction: row;
    width: 100%;
    margin: 0px;
    border-radius: 0px;
    padding: 15px;
  }  
  .onsite_barcode_{
    min-width: 60px;
    border: 0px;
    border-radius: 0px;
  }
  .classicSlideshow > div{
    opacity: 1 !important;
    transform: translate3d(0,0px,0);
  }
  .onsite_text_{
    font-size: 14px !important;
    padding: 0px 10px;
    text-align: left;
    margin-top: 0px;
  }
}
